.products{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 100%;  
}

.products_container{
    height: 100%;
    width: 100%;
    max-width: 1440px;  
    margin: 0 auto;
}

.products_block{
    padding: 0 10px;
    height: 100%;
    max-width: 1280px;  
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.products_block__below-filter{
    display: flex;
    height: 68px;
    width: 100%;
}

.products_pagination{
    margin-top: 20px; 
    margin-bottom: 40px;
    width: 317px;
    height:39px;
}

.products_container__filter{
    max-width: 1440px;
    width: 100%;
}

.products_container__mobile-filter{
    max-width: 100%;  
    display: none;
}

@media screen and (max-width: 1024px) {

    .products_container__banner{
        display: none;
    }
  }

  @media screen and (max-width: 767px) {
  
    .products_container__banner{
        display: flex;
    }

    .products_container__mobile-filter{
        display: flex;
    }

    .products_container__filter{
        display: none;
    }

  }

